  letter-spacing: 1px;
  margin: 8px auto;
  font-weight: 600;
}

.spinner__filmstrip {
  width: 56px;
  height: 70px;
  z-index: -1;
  background: #3d314a;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 8px #0000003d;
}

.spinner__filmstrip:before, .spinner__filmstrip:after {
  content: "";
  height: 120%;
  border-left: 5px dashed #f5f1ed;
  animation: .2s infinite roll;
  position: absolute;
}

.spinner__filmstrip:before {
  left: 5px;
}

.spinner__filmstrip:after {
  right: 5px;
}

@keyframes roll {
  0% {
    top: 0;
  }

  100% {
    top: -15px;
  }
}

